.ContentSmallSize {
    -ms-flex-positive: 0;
        flex-grow: 0;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    -ms-flex-preferred-size: calc(25% - 1em);
        flex-basis: calc(25% - 1em);
    text-align: right;
    margin-bottom: 1%;
    background-color: white;
}

.ContentSmallSize:hover {
    top: -2px;
    /*box-shadow: 0 4px 5px rgba(0,0,0,0.2);*/
    -webkit-box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
            box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

@media screen and (max-width: 800px) {
    .ContentSmallSize {
        -ms-flex-positive: 0;
            flex-grow: 0;
        -ms-flex-negative: 1;
            flex-shrink: 1;
        -ms-flex-preferred-size: calc(50% - 1em);
            flex-basis: calc(50% - 1em);
    }
}
@media screen and (max-width: 600px) {
    .ContentSmallSize {
        -ms-flex-positive: 0;
            flex-grow: 0;
        -ms-flex-negative: 1;
            flex-shrink: 1;
        -ms-flex-preferred-size: calc(100% - 1em);
            flex-basis: calc(100% - 1em);
    }
}

.ContentSmallSize figure {
   position: relative;
}
.ContentSmallSize figcaption {
    position: absolute;
    top: 180px;
    color: white;
    background-color: black;
    padding: 4px 8px;
    font-size: 100%;
    font-weight: 400;
}
.ContentSmallSize h3 {
    color: black;
}
.ContentSmallSize span {
    color: #999999;
}
.ContentSmallSize P {
    color: black;
}
.ContentSmallSize div {
    padding: 1% 2%;
}


